@import url("https://fonts.googleapis.com/css?family=Lato:300,400,700,900");
.dark_mode {
  background-color: #1a1a1a;
}
.dark_mode p {
  color: #FFFFFF;
}
.dark_mode a, .dark_mode h1, .dark_mode h2, .dark_mode h3, .dark_mode h4 {
  color: #e1e1e1;
}
.dark_mode .contenido-anuncio {
  background-color: #333333;
  border: 1px solid #333333;
}
.dark_mode .precio {
  color: #6a9919;
}
.dark_mode .iconos-caracteristicas img {
  filter: invert(100%);
}
.dark_mode .nosotros_texto blockquote {
  color: #e1e1e1;
}
.dark_mode .formulario label, .dark_mode .formulario legend {
  color: #FFFFFF;
}
.dark_mode .formulario input:not([type=submit]),
.dark_mode .formulario textarea, .dark_mode .formulario select {
  background-color: #333333;
  border: 1px solid #333333;
  color: #FFFFFF;
}
.dark_mode table.propiedades thead {
  color: #FFFFFF;
}
.dark_mode table.propiedades td {
  color: #FFFFFF;
}
.dark_mode table.propiedades tbody tr:nth-child(odd) {
  background-color: black;
}
.dark_mode .icono {
  background-color: #262626;
}
.dark_mode .entrada_texto h4::after {
  background-color: #6a9919;
}
.dark_mode .entrada_texto span {
  color: #E08709;
}
.dark_mode .nosotros_texto blockquote {
  color: #E08709;
}
.dark_mode .tabs .actual {
  background-color: #FFFFFF;
  color: #000000;
}
.dark_mode .caracteristicas {
  color: #000000;
  background-color: #FFFFFF;
  border: 1px solid #FFFFFF;
  -webkit-box-shadow: 2px 2px 1px 0px rgba(221, 221, 221, 0.7490196078);
  -moz-box-shadow: 2px 2px 1px 0px rgba(221, 221, 221, 0.7490196078);
  box-shadow: 2px 2px 1px 0px rgba(221, 221, 221, 0.7490196078);
}